@import url('https://fonts.googleapis.com/css2?family=Montserrat&family=Mulish&family=Poppins:ital,wght@0,400;0,500;0,600;1,300&display=swap');

*{
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}
:root{
  --color-bg: #2e2e32;
  --color-bg-variant: #D3D3D3;
  --color-primary: #2a2b2b;
  --color-primary-variant: rgba(77, 181, 255, 0.4);
  --color-white: #F5F5F7;
  --color-light: rgba(41, 40, 40, 0.6);

  --transition: all 400ms ease;

  --container-width-lg: 75%;
  --container-width-md: 86%;
  --container-width-ms: 90%;
}
html{
  scroll-behavior: smooth;
}
--webkit-scrollbar{
  display: none;
}
body{
  font-family: 'Poppins', sans-serif;
  background-color: var(--color-white);
  /* background-image: url("./assets/bg_texture.jpg"); */
  color: var(--color-bg);
  line-height: 1.7;
}
::-webkit-scrollbar {
  width: 12px;
}

::-webkit-scrollbar-thumb {
  background: linear-gradient(to bottom, transparent, var(--color-bg));
  border-radius: 6px;
}
/* =============================General Styles============================ */

.container{
  width: var(--container-width-lg);
  margin: 0 auto;
}
h1, h2, h3, h4, h5 { 
  font-weight: 500;
}
h1{
  font-size: 1.5rem;
}
section{
  margin-top: 8rem;
}

section > h2, section h5{
  text-align: center;
  color: var(--color-light);
}
section > h2{
  color: var(--color-primary);
  margin-bottom: 3rem;
}
.text-light{
  color: var(--color-light);
}
a{
  color: var(--color-primary);
  transition: var(--transition);
}
a:hover{
  color: var(--color-white);
}
.btn{
  width: max-content;
  display: inline-block;
  color: var(--color-primary);
  padding: 0.75rem 1.2rem;
  border-radius: 0.4rem;
  cursor: pointer;
  border: 1px solid var(--color-primary);
  transition: var(--transition);
}
.btn:hover{
  background: var(--color-white);
  color: var(--color-bg);
  border-color: transparent;
}
.btn-primary{
  background: var(--color-primary);
  color: var(--color-bg);
}
img{
  display: block;
  width: 100%;
  object-fit: cover;
}

/* =========Media quries (medium devices ========*/
@media screen and (max-width: 1024px) {
  .container{
    width: var(--container-width-md);
  }
  section{
    margin-top: 6rem;
  }
}

/* =========Media quries (small devices ========*/
@media screen and (max-width: 600px) {
  .container{
    width: var(--container-width-ms);
  }
  section > h2{
    margin-bottom: 2rem;
  }
}